<template>
    <div class="chart3d-div">
        <div id="chart3dContainer"/>
    </div>
</template>

<script>
    import * as THREE from 'three';

    export default {
        name: "Chart3d",
        data() {
            return {
                dataset: [
                    [0, 0, 0],
                    [1, 3, 1],
                    [2, 2, 2],
                    [2, 1, 1],
                ],
                camera: null,
                scene: null,
                renderer: null,
                staticMeshs: [],
                dynamicMeshs: {},
                staticLights: [],
                dynamicLights: {},
                count: 0,
            };
        },
        mounted() {
            this.initScene();
            this.animate();
        },
        methods: {
            /**
             * 初始化场景
             */
            initScene() {
                // 显示区域，用div吧。DIV Yes!
                const container = document.getElementById('chart3dContainer');

                this.renderer = new THREE.WebGLRenderer({antialias: true});
                this.renderer.setClearColor(0xcccccc);
                this.renderer.setSize(container.clientWidth, container.clientHeight)
                container.appendChild(this.renderer.domElement);

                // 新建一个摄像机
                // this.camera = new THREE.PerspectiveCamera(60, container.clientWidth / container.clientHeight, 1, 1000);
                // this.camera.position.z = 32;
                this.camera = new THREE.OrthographicCamera(-30, 15, -20, 10, 0, 10);
                this.camera.position.set(5, 10, 9);

                // 新建一个场景
                this.scene = new THREE.Scene();
                // let cameraHelper = new THREE.CameraHelper(this.camera);
                // this.scene.add(cameraHelper);

                for (let idx in this.dataset) {
                    // let material = new THREE.MeshBasicMaterial({color: 0xffffff});
                    let material = null;
                    switch (idx + '') {
                        case '0': {
                            material = new THREE.MeshBasicMaterial({color: 0xff0000});
                            break;
                        }
                        case '1': {
                            material = new THREE.MeshBasicMaterial({color: 0x00ff00});
                            break;
                        }
                        case '2': {
                            material = new THREE.MeshBasicMaterial({color: 0x0000ff});
                            break;
                        }
                        default : {
                            material = new THREE.PointsMaterial({
                                color: 0xffffff,
                                size: 20,
                                transparent: true,
                                depthTest: false
                            });
                        }
                    }
                    // let material = new THREE.MeshBasicMaterial({color: 0xffffff});
                    let geometry = new THREE.SphereGeometry();
                    let sphere = new THREE.Mesh(geometry, material);
                    sphere.position.x += (this.dataset[idx][0] * 3);
                    sphere.position.y += (this.dataset[idx][1] * 3);
                    sphere.position.z += (this.dataset[idx][2] * 3);
                    this.staticMeshs.push(sphere);
                }

                for (let idx in this.staticMeshs) {
                    this.scene.add(this.staticMeshs[idx]);
                }

                // 神说：要有光！
                const light = new THREE.AmbientLight('#ffffff', 1);
                this.staticLights.push(light);

                // var spotLight = new THREE.SpotLight(0xFFFFFF, 1, 100);
                // spotLight.position.set(0, 4, 0);
                // this.staticLights.push(spotLight);
                // 添加光
                for (let idx in this.staticLights) {
                    this.scene.add(this.staticLights[idx]);
                }

                this.renderer.render(this.scene, this.camera)

                // this.camera.lookAt(this.staticMeshs[0].position);
            },
            loadDataset() {

            },
            animate() {
                requestAnimationFrame(this.animate);
                // this.camera.lookAt(this.staticMeshs[this.count++ % this.dataset.length].position);
                this.renderer.render(this.scene, this.camera);
            },
        },
    };


</script>

<style scoped>
    #chart3dContainer {
        width: 374px;
        height: 256px;
    }
</style>